	module.exports = {"data":{"content":{"content":"<h2 id=前言>前言</h2>\n<p>如果沒有做任何配置的話，<code>hexo g</code>的時候會自動輸出一個<code>sitemap.xml</code>。\n但是現在域名不同了，需要一點更改。。。</p>\n<h2 id=方法>方法</h2>\n<ol>\n<li>打開_config.yml</li>\n<li>修改<code>url:</code>就可以了</li>\n<li><code>hexo clean</code></li>\n<li><code>hexo g</code></li>\n</ol>\n<h2 id=參考資料>參考資料</h2>\n<ol>\n<li><a href=\"https://segmentfault.com/q/1010000004659915\">hexo-generator-sitemap 生成的sitemap如何配置自定义的域名？</a></li>\n</ol>\n","title":"Hexo 時代之更改 sitemap 域名","createdDate":"2017-02-26T14:45+08:00","headerImgur":null,"id":"5L1s4C248MSIGQMqwEEcAk","toc":["前言","方法","參考資料"],"tags":"Hexo, SEO"},"latestPosts":{"totalCount":60,"edges":[{"node":{"title":"【網易考拉校招】部門總監面對面-直播答疑邀約（附網易內推碼B222E54）","url":"netease-kaola-live","createdDate":"2018-08-01T20:00+08:00"}},{"node":{"title":"Uncaught ReferenceError: regeneratorRuntime is not defined 解決方法","url":"regenerator-runtime-is-not-defined","createdDate":"2018-07-29T20:00+08:00"}},{"node":{"title":"關於作者","url":"about-calpa-liu","createdDate":"2018-05-01T20:00+08:00"}},{"node":{"title":"【方法】在直接複製貼上內容的時候，可能已經不知不覺注入了一段不可視的「空白」","url":"insert-zero-width-characters","createdDate":"2018-04-06T23:00+08:00"}},{"node":{"title":"知乎無限 live 數據流網站","url":"zhihu-unlimited-lives-website","createdDate":"2018-03-28T20:49+08:00"}},{"node":{"title":"Gatsbyjs async/await 更新報錯解決方法","url":"gatsybjs-async-await-upgrade-bug-solution","createdDate":"2018-03-21T20:00+08:00"}}]}},"pathContext":{"id":"5L1s4C248MSIGQMqwEEcAk"}}

